@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'proxima-nova', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff !important
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #213743 transparent;
}

.user-menu-triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #FFFFFF transparent;
}

.wallet-coin-triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #2f4553 transparent;
}

.white-triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #FFFFFF transparent;
}

.block-box-shadow {
  box-shadow: rgb(33, 55, 67) 0px 4.595px 0px 0px;
}

.selected-box-shadow {
  box-shadow: #7100c7 0px 4.595px 0px 0px;
}

.slide-gradient-cover {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #071824, transparent, #071824);
}

.card-group-container {
  transition: left 10000ms cubic-bezier(0.24, 0.78, 0.15, 1);
}

.custom-input-error {
  border: 1px solid #ed4163 !important;
}

.third-party-auth-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.third-party-auth-container {
  max-width: 200px;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 4px;
}

.third-party-auth-container:before {
  content: "";
  display: inline-flex;
  height: 1px;
  background: #b1bad3;
  flex: 1;
  opacity: 0.3;
}

.third-party-auth-container:after {
  content: "";
  display: inline-flex;
  height: 1px;
  background: #b1bad3;
  flex: 1;
  opacity: 0.3;
}

.social-casino {
  background-image: url('/public/images/casino-poker-cards-green-en.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.is-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

::-webkit-scrollbar {
  width: 2px;
}

::-webkit-scrollbar-track {
  background: #535353;
}

::-webkit-scrollbar-thumb {
  background: #3e3e3f;
}

.custom-notification {
  background-color: #213743;
  color: White;
}

.ant-notification-notice {
  background-color: #213743 !important;
  border-radius: 4px !important;
  padding: 10px !important;
}

.ant-notification-notice-message {
  color: White !important;
}

.ant-notification-notice-description {
  color: White !important;
}